import {ModuleWithProviders, NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {FormsModule} from '@angular/forms';

import {AdaptDropdownModule} from '../dropdown/index';
import {ClockComponent} from './clock.component';
import {AdaptTimeComponent} from './time.component';
import {AdaptIconModule} from '../icon/index';
import {AdaptTooltipModule} from '../tooltip/index';
import {AdaptPopoverModule} from '../popover/index';
import {AdaptDragonInputHandlerModule} from '../dragon-input-handler/index';
import {AdaptRxLabelModule} from '../rx/form-control/index';


@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    AdaptDropdownModule,
    AdaptIconModule,
    AdaptTooltipModule,
    AdaptPopoverModule,
    AdaptDragonInputHandlerModule,
    AdaptRxLabelModule
  ],
  declarations: [ClockComponent, AdaptTimeComponent],
  exports: [ClockComponent, AdaptTimeComponent]
})
export class AdaptTimeModule {
  /**
   * Importing with '.forRoot()' is no longer necessary, you can simply import the module.
   * Will be removed in a short while
   *
   * @deprecated 11.16.0
   */
  static forRoot(): ModuleWithProviders<AdaptTimeModule> {
    return {
      ngModule: AdaptTimeModule
    };
  }
}

export * from './clock.component';
export * from './time.component';
